// TODO Need docs

let animationLock = false;

const scrollTo = targetY => {
  if (targetY < 0) return;
  if (animationLock) return;

  animationLock = true;

  // NOTE: iOS と PC でスクロール位置の計算が異なるため。またIE対策。
  const scrollingElement = document.scrollingElement || document.documentElement;

  // 上方向かどうか
  const upward = scrollingElement.scrollTop > targetY;

  // 対象の要素までのスクロール距離
  const distance = () => Math.abs(scrollingElement.scrollTop - targetY);

  let step = Math.round(distance() / 50);
  let interval = 10;

  let prevScrollTop =  scrollingElement.scrollTop;

  const anime = () => {
    if (distance() <= 1) {
      scrollingElement.scrollTop = targetY;
      animationLock = false;
      return;
    }

    if (distance() < step) {
      step = 2;
      interval = 1;
    }

    if (upward) {
      if (scrollingElement.scrollTop <= targetY) {
        animationLock = false;
        return;
      }
      scrollingElement.scrollTop -= step;
    } else {
      if (scrollingElement.scrollTop >= targetY) {
        animationLock = false;
        return;
      }
      scrollingElement.scrollTop += step;
    }

    /*
    if (scrollingElement.scrollTop === prevScrollTop) {
      animationLock = false;
      return;
    }
    */

    prevScrollTop = scrollingElement.scrollTop;

    setTimeout(anime, interval);
  };

  anime();
};

export default scrollTo;
